#cardsContainer{
    display: flex;
}

#card{
    border: 2px solid black;
    width: 200px;
    margin: 20px;
    border-radius: 2%;
}

#cardheader{
    text-align: center;
    background-color: rgb(20, 20, 20);
    color: whitesmoke;
    height: 100;
    position: relative;
 
}

#rank{
    color: white;
    border: 1px solid black;
    position: absolute;
    z-index: 1;
    background-color: rgb(36, 36, 36);
    opacity: 75%;
}

img{
        height: 300px;
        height: 300px;
        position: relative;
        right: 90px;
        margin: 0px;
}

#imgcont{
    overflow: hidden;
    width: 200px;
    height: 250px;
    margin: 0px;
}

hr,h3,h2{
    margin: 0;
}

#cardfooter{
    color: white;
    background-color: black;
    text-align: center;
}